export class Device {
  acl: any[];
  banner_failed: string;
  banner_motd: string;
  banner_welcome: string;
  comment: string;
  connection_timeout: number;
  created_at: string;
  disabled: number;
  enable: string;
  enable_flag: number;
  group: any[];
  id: number;
  address: any[];
  key: string;
  manual: string;
  model: string;
  name: string;
  sn: string;
  type: string;
  updated_at: string;
  user_group: any[];
  vendor: string;

    empty(): void {
      this.acl=[];
      this.banner_failed='';
      this.banner_motd='';
      this.banner_welcome='';
      this.comment='';
      this.connection_timeout=undefined;
      this.created_at='';
      this.disabled=0;
      this.enable='';
      this.enable_flag=1;
      this.group=[];
      this.id=undefined;
      this.address=[];
      this.key='';
      this.manual='';
      this.model='';
      this.name='';
      this.sn='';
      this.type='';
      this.updated_at='';
      this.user_group=[];
      this.vendor='';
    }
}
