import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-tac-device-form',
  templateUrl: './tac-device-form.component.html',
  styleUrls: ['./tac-device-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TacDeviceFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  objectKeys = Object.keys;


  list = {
    group: {
      multiple: false,
      title: 'Device Group',
      title_sidebar: 'Device Groups List',
      search: true,
      apiurl: 'api/tacacs/device/group/list/',
    },
    acl: {
      multiple: false,
      title: 'Access Control List',
      title_sidebar: 'ACL List',
      search: true,
      apiurl: 'api/tacacs/acl/list/',
    },
    user_group: {
      multiple: false,
      title: 'Default User Group',
      title_sidebar: 'User Group List',
      search: true,
      apiurl: 'api/tacacs/user/group/list/',
    },
    address: {
      multiple: false,
      title: 'Address',
      title_sidebar: 'Address List',
      search: true,
      apiurl: 'api/obj/address/list/',
      addNew: 'address',
      filterParams: ['address'],
      pop: {
        content: (item, i) => {
          return '<table>'+
            '<tr><td>Name:</td><td><b>'+item.text+'</b></td></tr>'+
            '<tr><td>Type:</td><td><b>'+((!!item.type) ? 'IPv6' : 'IPv4')+'</b></td></tr>'+
            '<tr><td>Address:</td><td><b>'+item.address+'</b></td></tr>'+
          '</table>'
        },
      }
    },
  }

  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  setGroup(data) {
    //console.log(data)
    this.options.data.group = data
  }

  setAcl(data) {
    console.log(data)
    this.options.data.acl = data
  }

  setUserGroup(data) {
    //console.log(data)
    this.options.data.user_group = data
  }

  setAddr(data) {
    //console.log(data)
    this.options.data.address = data
  }

}
